@at-root {

  @import "node_modules/sass-toolkit/stylesheets/toolkit/clearfix";



  ////------------------------------------------
  .beerListItem {
    //------------------------------------------

    // ----- Layout -----
    position : relative;
    @include clearfix;

    // ----- Content layout -----
    .liquid-container, .liquid-child {
      overflow : visible; } }



  ////------------------------------------------
  .beerListItem-main {
    //------------------------------------------

    // ----- Layout -----
    //display   : flex

    // ----- Content layout  -----
    .beerListItem-image {
      //flex-shrink : 0
      //flex-grow   : 0
      float : left;

      $size  : 50px;
      width  : $size;
      height : $size;

      object-fit    : cover;
      border-radius : 50%;

      transition : width 0.5s ease, height 0.5s ease, border-radius 0.5s ease;

      margin-top    : 0.5em;
      margin-right  : 1em;

      .beerListItem.-isExpanded & {
        margin-bottom : 1rem; } }

    //.beerListItem-main-content
    ////flex-shrink : 1
    ////flex-grow   : 0

    .beerListItem.-isExpanded & .beerListItem-image {
      width         : 200px;
      height        : auto;
      border-radius : 0; } }




  ////------------------------------------------
  .beerListItem-titleAndBrewery {
    //------------------------------------------

    // ----- Layout -----
    display     : flex;
    flex-wrap   : wrap;
    align-items : baseline;

    // ----- Content layout -----
    > * {
      flex-shrink  : 1;
      flex-grow    : 0;

      position : relative;
      z-index  : 2;

      &:not(:last-child) {
        margin-right : 0.5rem; } } }



  ////------------------------------------------
  .beerListItem-tap {
    //------------------------------------------

    // ----- Content -----
    &:before {
      content : '№'; } }



  ////------------------------------------------
  .beerListItem-title {
    //------------------------------------------

    // ----- Text -----
    font-size : 1.5rem; }



  ////------------------------------------------
  .beerListItem-title-link {
    //------------------------------------------

    // ----- Text -----
    text-decoration: none;
    border-bottom: 1px dashed; }



  ////------------------------------------------
  .beerListItem-content {
    //------------------------------------------

 }    // ----- Layout -----



  ////------------------------------------------
  .beerListItem-fields {
    //------------------------------------------

    // ----- Layout -----
    display     : flex;
    flex-wrap   : wrap;
    align-items : baseline;

    // ----- Content Layout -----
    .beerListItem-field {
      &:not(:last-child) {
        margin-right : 1em;

        &:after {
          content : ','; } } } }



  ////------------------------------------------
  .beerListItem-field {
    //------------------------------------------

    // ----- Looks -----
    border-bottom : 1px dotted;
    cursor        : help; }



  ////------------------------------------------
  .beerListItem-field-value-icon {
    //------------------------------------------

    // ----- Layout -----
    width : 15px; }




  ////------------------------------------------
  .beerListItem-image {
    //------------------------------------------

    // ----- Layout -----
    cursor        : pointer; } }



