@mixin stretch {
  position : absolute;
  top      : 0;
  left     : 0;
  width    : 100%;
  height   : 100%; }

@mixin overstretch($percentage : 1%) {
  position : absolute;
  top      : -$percentage;
  left     : -$percentage;
  width    : 100% + $percentage * 2;
  height   : 100% + $percentage * 2; }
